
.checkin-modal {
    .ivu-modal-close {
        border-radius: 50%;
        width: 24px;
        height: 24px;
        color: #fff;
        background-color: rgba(255, 255, 255, .2);
        text-align: center;
        line-height: 24px;
        i {
            vertical-align: middle;
            line-height: 24px;
            font-size: 28px;
            position: initial;
            color: #fff;
        }
    }
    .ivu-modal-header {
        text-align: center;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        font-weight: normal;
        background-image: linear-gradient(45deg, #efb946, #ef8a46);
    }
    .ivu-modal-footer{
      // border-top: 0;
    }
    .checkin-title {
        margin-top: 15px;
        width: 100%;
        text-align: center;
        font-size: 22px;
        font-weight: normal;
        color: #fff;
    }

    .checkin-total {
        border-radius: 14px;
        margin: 15px auto;
        width: 120px;
        height: 28px;
        line-height: 28px;
        text-align: center;
        color: #d46c28;
        font-weight: normal;
        background-color: rgba(255, 255, 255, .2);
        font-size: 16px;
    }

    .checkin-body {
        .checkin-reward {
            width: 100%;
            text-align: center;
            color: #d46c28;
            font-size: 32px;
        }
        .checkin-tips {
            text-align: center;
            font-size: 14px;
            color: #999;
        }
        .checkin-btn {
            margin-top: 20px;
            height: 45px;
            font-size: 18px;
            color: #fff;
            border: 0;
            background-image: linear-gradient(45deg, #efb946, #ef8a46);
            &[disabled] {
                background: rgb(204, 204, 204);
            }
        }
    }
    .checkin-footer {
        display: flex;
        justify-content: center;
        border: 0;
        .checkin-index {
            display: flex;
            flex-direction: column;
            align-items: center;
            + .checkin-index{
              margin-left: 20px;
            }
            img{
              display: block;
              width: 35px;
              height: 35px;
              border-radius: 50%;
            }
            i {
                font-size: 15px;
                color: #ccc;
                font-style: normal;
            }
        }
    }
}



