






















































































































.expertList {
  .ivu-modal {
    top: 0;
    margin: 0;
    height: 100%;
    .ivu-modal-content {
      border-radius: 0;
      height: 100%;
      .ivu-modal-header {
        padding: 8px 0;
        .ivu-input-group-prepend {
          background-color: #efefef;
          border: none;
        }
        .ivu-input {
          background-color: #efefef;
          border-radius: 6px;
          border-left: none;
          border: none;
          font-size: 16px;
          &:focus, &:hover {
            border: none;
            box-shadow: none;
          }
        }
      }
      .ivu-modal-body {
        padding: 0;
        height: calc(~"100% - 52px");
        overflow: scroll;
        font-size: 16px;
      }
      .ivu-modal-footer {
        padding: 0;
        border-top: none;
      }
    }
  }
}



