



































.prePostRoot {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  overflow: hidden;
  background: rgba(255, 255, 255, .9);
  z-index: 9;
  display: block;
  height: 100vh;
  width: 100vw;
  .container {
    position: fixed;
    bottom: 0;
    width: 100vw;
    .entryRow {
      padding-top: 24px;
      &.lastEntryRow {
        padding-bottom: 24px;
      }
      .entryCol {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        img {
          width: 60%;
        }
        span {
          margin-top: 8px;
          font-size: 14px;
          color: #333;
        }
      }
    }
    .closeButton {
      height: 55px;
      background-color: #fff;
      border-radius: 0;
      border: none;
      border-top: 1px solid #ededed;
    }
  }
}



