











































































.vertical-center-modal {
    display: flex;
    align-items: center;
    justify-content: center;

    .ivu-modal {
        top: 0;
    }
}

.pinne-block {
    +.pinne-block {
        border-top: 1px solid #ededed;
    }
    padding: 10px 0;
    label {
        width: 100%;
    }
}

.choose-days {
    margin-top: 10px;
    display: flex;
    justify-content: space-around;
    &> {
        flex: 1 1 auto;
    }
}



