

















.singleLocked {
  .notSquare {
    position: relative;
    overflow: hidden;
    max-height: 80vw;
    img {
      width: 100%;
      display: block;
      height: 100%;
      object-fit: cover;
    }
    div {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  
}
.locked{
  height: 100%;
  width: 100%;
  .square {
    position: relative;
    img {
      width: 100%;
      display: block;
      height: 100%;
      object-fit: cover;
    }
    div {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
  .notSquare {
    height: 100%;
    width: 100%;
    position: relative;
    height: 100%;
    img {
      width: 100%;
      display: block;
      height: 100%;
      object-fit: cover;
    }
    div {
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}



