


























.postAnswer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  overflow: hidden;
  background: #fff;
  z-index: 10;
  display: block;
  height: 100%;
  width: 100%;
  .answerBody {
    .contentRow {
      padding-top: 12px;
      padding-bottom: 12px;
      border-bottom: 1px solid #ededed;
      textarea {
        border: none;
        box-shadow: none;
      }
    }
  }
}



