  overflow: auto;
  position: fixed!important;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 9!important;
  margin: auto;
  background-color: #000;
  transition: transform .9s;
  transform:scale(1);
  .swiperSlide {
    display: flex!important;
    justify-content: center;
    align-items: center;
  }
  .swiper-pagination-bullets {
    top: 15px;
    bottom: initial!important;
    .swiper-pagination-bullet {
      background-color: #aaa;
    }
    .swiper-pagination-bullet.swiper-pagination-bullet-active {
      background-color: #fff;
    }
  }
}
.swiper-lazy-loaded {
  width: 100%!important;
}
.payButton {
  color: #fff;
  position: absolute;
  padding: 4px 8px;
  background-color: #e20000 ;
  border-radius: 5px;
  bottom: 8vh;
  left: calc(~"50% - 36px");
}
.noRelative {
  position: initial;
  height: 100%;
  img {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
  }
}




