
























































































































































































































.postRoot {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  overflow: hidden;
  background: #efefef;
  z-index: 10;
  display: block;
  height: 100%;
  width: 100%;
  .stepOne {
    background-color: #fff;
    .titleRow {
      padding-top: 12px;
      padding-bottom: 12px;
      border-bottom: 1px solid #ededed;
      .title {
        width: 100%;
      }
    }
  }
  .stepTwo {
    background-color: #fff;
    height: 100%;
    .contentRow {
      padding-top: 12px;
      padding-bottom: 12px;
      border-bottom: 1px solid #ededed;
      .content {
        border: none;
        textarea {
          border: none;
          box-shadow: none;
        }
      }
    }
    .toolBar {
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      width: 100vw;
      padding-bottom: 12px;
      padding-top: 12px;
      border-bottom: 1px solid #ededed;
      border-top: 1px solid #ededed;
      height: 47px;
      .contentAction {
        .upload {
          div {
            display: block;
          }
        }
        .poptip {
          div {
            display: block;
          }
        }
      }
    }
  }
  .stepThree {
    background-color: #fff;
    .topicList {
      padding: 12px 12px 0 12px;
      .topic {
        background-color: #59b6e7;
        font-size: 16px;
        border-radius: 15px;
      }
    }
    .topicSearchRow {
      padding-top: 12px;
      padding-bottom: 12px;
      border-bottom: 1px solid #ededed;
      .searchKey {
        border: none;
        div {
          border: none;
          background-color: #fff;
        }
        input {
          border: none;
          font-size: 16px;
          &:focus, &:hover{
            border: none;
            outline: 0;
            box-shadow: none;
          }
        }
      }
    }
  }
  .stepFour {
    .setAmount {
      background-color: #fff;
      .RowLide {
        padding-top: 12px;
        padding-bottom: 12px;
        .button {
          width: 100%;
        }
        &:last-child {
          border-bottom: 1px solid #ededed;
        }
      }
      .textAlignLeft {
        text-align: left;
      }
      .balanceInput {
        width: 100%;
      }
    }
    .setReward {
      background-color: #fff;
      margin-top: 12px;
      background-color: #fff;
      .rewardRow {
        padding-top: 12px;
        padding-bottom: 12px;
        border-bottom: 1px solid #efefef;
        &:last-child {
          border-bottom: none;
        }
      }
    }
    .rewardRuleEntry {
      margin-top: 12px;
      button {
        background: none;
      }
    }
    .publish {
      margin-top: 12px;
    }
  }
}



